import React, {useState} from 'react'

export default function Textform(props) {
 const handleUpClick=()=>{
     let newText=text.toUpperCase()
     setText(newText)
     console.log("clicked")
 }

const handleOnChange=(event)=>{
console.log("handleonchnage")
setText(event.target.value)
 }
const handleLoClick=()=>{
    let newText=text.toLowerCase()
     setText(newText)
     console.log("clicked")
}


const handleWhiteSpaces=()=>{       
let newText=text.split(/[ ]+/)
setText(newText.join(" "))

}
const handleCopy=()=>{
    var text=document.getElementById("myBox")
    text.select()
    text.setSelectionRange(0,10000)
    navigator.clipboard.writeText(text.value)

}
const handleProperCase=()=>{
let words=text.split(" ")
let capitalizeWords=[]
words.forEach(element=>{
    capitalizeWords.push(element[0].toUpperCase()+element.slice(1,element.length))
})
setText(capitalizeWords.join(" "))
}

const clearText=()=>{
    setText("")
}
 const [text, setText] = useState("enter text here")

    return (
        <>
        <div className="container my-5">
           
<div className="mb-3">
  <h1>{props.title}</h1>
  <textarea className="form-control" value={text} onChange={handleOnChange}  id="myBox" rows="8"></textarea>
</div>
<button onClick={handleUpClick} type="button" className="btn btn-primary mx-1 my-1">Change to Uppercase</button>
<button onClick={handleLoClick} type="button" className="btn btn-primary mx-1 my-1">Change to LowerCase</button>
<button onClick={handleWhiteSpaces}  type="button" className="btn btn-primary mx-1 my-1">Remove WhiteSpaces</button>
<button onClick={handleCopy} type="button" className="btn btn-primary mx-1 my-1">Copy</button>
<button onClick={clearText} type="button" className="btn btn-primary mx-1 my-1">Clear text</button>
<button onClick={handleProperCase} type="button" className="btn btn-primary mx-1 my-1">Change to Proper Case</button>
</div>
<div className="container">
<h4>Your Text Preview here</h4>
<p>Total {text.split(" ").filter((element)=>{return element.length!==0}).length} words</p>
<h5>Preview</h5>
<p>{text.length>0?text:"enter your text to preview"}</p>

</div>
</>        
         )
}
